@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
$primary-color: #2c3e50;
$silver-color: #bdc3c7;
$white-color: #fff;
$cloud-color: #ecf0f1;
$shadow-outside: 4px 5px 9px 0px #0000004d, -4px -5px 11px 0px #fff;
$shadow-inside: 4px 5px 9px 0px #00000038 inset,
  -4px -5px 11px 0px #ffffff4a inset;
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
html {
  font-size: 62.5%;
}
body {
  background-color: $silver-color;
}
@mixin headings {
  font-size: 4rem;
  font-weight: 600;
  line-height: 5rem;
  color: $primary-color;
}
h1.heading_style {
  @include headings;
  text-align: center;
  padding: 10px 0;
}
hr.line {
  margin: 20px 0;
}
.slot_inner {
  display: block;
  margin: 20px 0;
  position: relative;
  //   left: 40%;
  background-color: $silver-color;
  padding: 40px;
  box-shadow: $shadow-outside;
  border-radius: 12px;
  transition: 1s ease;
  cursor: default;
  width: 100%;
}
.slot_inner:hover {
  transition: all 0.5s ease;
  box-shadow: $shadow-inside;
}
span.code_emoji {
  text-align: center;
  font-size: 5rem;
  justify-content: center;
  display: flex;
}
h6.output {
  font-size: 2rem;
  color: $cloud-color;
  text-align: center;
}
.SlotM_parent {
  width: 50%;
  margin: auto;
}
