@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Poppins", sans-serif}html{font-size:62.5%}body{background-color:#bdc3c7}h1.heading_style{font-size:4rem;font-weight:600;line-height:5rem;color:#2c3e50;text-align:center;padding:10px 0}hr.line{margin:20px 0}.slot_inner{display:block;margin:20px 0;position:relative;background-color:#bdc3c7;padding:40px;box-shadow:4px 5px 9px 0px rgba(0,0,0,0.301961),-4px -5px 11px 0px #fff;border-radius:12px;transition:1s ease;cursor:default;width:100%}.slot_inner:hover{transition:all 0.5s ease;box-shadow:4px 5px 9px 0px rgba(0,0,0,0.219608) inset,-4px -5px 11px 0px rgba(255,255,255,0.290196) inset}span.code_emoji{text-align:center;font-size:5rem;-webkit-justify-content:center;justify-content:center;display:-webkit-flex;display:flex}h6.output{font-size:2rem;color:#ecf0f1;text-align:center}.SlotM_parent{width:50%;margin:auto}

